nav li {    
        text-decoration: none;
        list-style: none;        
}

//layout
#nav-site {
    display: flex;
    align-items: center; 
    ul {        
        display: flex;                
        align-self: center;
        width: 100%;
        height: 15px;
        
        // flex-direction: column;
        // justify-content: space-between; 
       
        li{                       
            text-align: center;
            width: 25%;
            border-right: 2px $color__main-light solid;
        }
        
        li:last-child{
            border-right: none;
        }
    }
}


#nav-ecommerce {
    ul{
        display: flex;      
        li{
            text-align: center;
            background-color: $color__main;
            padding: 25px 0;
            width: 25%;
            border-right: 0.1px $color__main-light solid;
            min-width: 150px;           
        }
        
    }
}