li {
  text-decoration: none;
  list-style: none; }

ul {
  margin: 0 0 0 0;
  padding: 0; }

nav li {
  text-decoration: none;
  list-style: none; }

#nav-site {
  display: flex;
  align-items: center; }
  #nav-site ul {
    display: flex;
    align-self: center;
    width: 100%;
    height: 15px; }
    #nav-site ul li {
      text-align: center;
      width: 25%;
      border-right: 2px #C9D990 solid; }
    #nav-site ul li:last-child {
      border-right: none; }

#nav-ecommerce ul {
  display: flex; }
  #nav-ecommerce ul li {
    text-align: center;
    background-color: #94B82F;
    padding: 25px 0;
    width: 25%;
    border-right: 0.1px #C9D990 solid;
    min-width: 150px; }

body {
  width: 100vw; }

header {
  display: grid;
  grid-template-rows: 40px 40px 40px auto;
  grid-template-columns: 15% 40% auto;
  font-family: "Comfortaa";
  border: 1px solid #94B82F; }
  header #nav-site {
    grid-column: 3 / 4;
    grid-row: 1 / 1;
    font-size: calc(0.65em + 0.2vw); }
  header .logo {
    justify-self: left;
    height: inherit;
    grid-column-start: 1;
    grid-row: 1 / 4; }
    header .logo svg {
      height: 90%;
      width: 90%;
      min-width: 50px; }
  header .search-box {
    display: flex;
    min-width: 150px;
    grid-column: 2 / 3;
    grid-row: 2 / 2;
    background-color: white;
    border: 2px solid #94B82F; }
    header .search-box input {
      border: #C9D990;
      margin: auto;
      width: 80%; }
  header #user-area {
    grid-row-start: 2;
    background-color: #C9D990;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-bottom-left-radius: 60px;
    margin-left: 10px; }
  header #nav-ecommerce {
    grid-column: 1 / 4;
    grid-row: 4 / 4;
    justify-content: center;
    font-weight: bolder;
    font-size: 1.6em;
    color: white; }
  header #claim {
    grid-row: 3 / 3;
    margin: auto 0;
    font-weight: bold;
    color: #94B82F; }

@media screen and (max-width: 650px) {
  header {
    font-size: 0.8em;
    grid-template-columns: auto auto auto; }
  .search-box {
    grid-column: 1 / 1; }
  #nav-site {
    grid-column: 2 / 4 !important;
    height: 30px;
    margin-right: 20px; }
    #nav-site ul li {
      margin: auto; }
  #user-area {
    min-width: 90px; }
    #user-area span {
      display: none; }
  #claim {
    display: none; }
  #nav-ecommerce {
    display: none; }
    #nav-ecommerce ul {
      justify-content: stretch;
      flex-wrap: wrap; }
      #nav-ecommerce ul li {
        width: 49%;
        margin: 1px auto; } }

.brand-message {
  margin: 20px 0; }
  .brand-message ul {
    height: 70px;
    background-color: #F3F3F3;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    .brand-message ul li {
      display: flex;
      align-items: center;
      margin: auto; }
      .brand-message ul li span {
        width: 50%; }
      .brand-message ul li .fas {
        margin-right: 15px;
        font-size: calc(20px + 0.5vw); }

.wrapper-home {
  width: 60%;
  margin: auto; }

ul > li > ul > li {
  display: none; }

.icon-mediumlight {
  border: none;
  background-color: white; }
  .icon-mediumlight i {
    color: #94B82F; }

.icon {
  color: grey;
  font-size: 1.2em;
  margin: 0 4px; }
